import React from 'react';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';

export default function CheckoutSteps(props) {
  return (
    <Row className="checkout-steps">
      <Col className={props.step1 ? 'active' : ''}>ng nhp</Col>
      <Col className={props.step2 ? 'active' : ''}>Nhp a ch</Col>
      <Col className={props.step3 ? 'active' : ''}>Chn hnh thc thanh ton</Col>
      <Col className={props.step4 ? 'active' : ''}>Cht n</Col>
    </Row>
  );
}